@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #0B2826 !important;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;

  color: #F7DEB7;
  font-family: 'Comfortaa', Arial, Helvetica, sans-serif;
  font-size: 1rem;
}

#slide0 {
  min-height: 1000px;
}

.App {
  text-align: center;
  margin-bottom: 20rem;
}

.Banner {
  margin-top: 100px;
}

.Form {
  text-align: left;
}

.Button {
  display: block;
  text-align: right;
  margin-top: 1rem;
}

p {
  color: #E2B35E;
  font-family: 'Comfortaa', Arial, Helvetica, sans-serif;
  font-size: 1rem;
}

input {
  background-color: #F2F7F7;
  border: none;
  border-bottom: 0.3rem solid #65C3A5;
  height: 3rem;
  color: #0B2826;
  font-family: 'Comfortaa', Arial, Helvetica, sans-serif;
  font-size: 1rem;
  padding-top: 0.3rem;
  padding-left: 0.6rem;
  width: 690px;
}

button {
  border: none;
  background-color: #65C3A5;
  color: #072E20;
  height: 3rem;
  padding: 1rem 2rem;
  font-family: 'Comfortaa', Arial, Helvetica, sans-serif;
  font-size: 1rem;
}
